class About extends React.Component {

	constructor (props) {
		super(props)
		this.ref = React.createRef()
		this.state = {
			id: "about"
		}
	}

	componentDidMount() {
		this.props.offsets.push(this.ref.current.offsetTop)
	}
	
	render () {
		return (
			<section id={this.state.id} className="about" ref={this.ref}>
				<div className='wrap'>
					<h3 className="about__header">{this.props.title}</h3>
					<p className="about__desc">{this.props.description}</p>
				</div>
			</section>
		)
	}
}

export default About
